import axios from '@/libs/api.request';
/**
 * 添加
 */

var obj = new Object();

obj.addSnjb = function (data, fnOk, fnError) {
  return axios.request({
    url: 'snJbjPzjl/addTemplate',
    method: 'post',
    data: data
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};
