import Tables from "_c/tables";
import Edit from "./snjb-edit.vue";
import Add from "./snjb-add.vue";
import Detail from "./snjb-detail.vue";
import snjbApi from "@/api/snjb-api";
import MIXIN_ROLE from "@/mixin/ROLE";
export default {
  mixins: [MIXIN_ROLE],
  // 引入mixin之后，即可通过this.ROLE获取到权限信息
  components: {
    Tables: Tables,
    Edit: Edit,
    Add: Add,
    Detail: Detail
  },
  data: function data() {
    var _this = this;

    return {
      editModal: {
        show: false
      },
      addModal: {
        show: false
      },
      detailModal: {
        show: false
      },
      detail: {},
      columns2: [//      { title: "模板名称", key: "templateName" },
      {
        title: "桩号",
        key: "pileStartNum"
      }, {
        title: "成桩开始时间",
        key: "finishPileStartTime"
      }, {
        title: "成桩结束时间",
        key: "finishPileEndTime"
      }, {
        title: "日完成桩数",
        key: "dayFinishCount"
      }, {
        title: "日完成量",
        key: "dayFinishVolume"
      }, {
        title: "操作",
        key: "action",
        //  fixed: 'right',
        //  width: 120,
        render: function render(h, params) {
          return h("div", [h("Button", {
            props: {
              type: "success",
              size: "small"
            },
            on: {
              click: function click(e) {
                _this.getDetail(_this.snjbList[params.index], function () {
                  _this.detailModal.show = true;
                }); //                    this.detailModal.show = true;
                //                    this.detail = this.snjbList[params.index];
                //                    console.log(this.detail);

              }
            },
            style: {
              marginRight: "5px"
            }
          }, "查看"), h("Button", {
            props: {
              type: "warning",
              size: "small"
            },
            on: {
              click: function click(e) {
                _this.getDetail(_this.snjbList[params.index], function () {
                  _this.editModal.show = true;
                }); //                    this.editModal.show = true;
                //                    this.detail = this.snjbList[params.index];
                //                    console.log(this.detail);

              }
            },
            style: {
              marginRight: "5px"
            }
          }, "修改"), h("Poptip", {
            props: {
              confirm: true,
              title: "你确定要删除吗?"
            },
            on: {
              "on-ok": function onOk() {
                console.warn(params.index);
                snjbApi.deleteSnjb({
                  id: _this.snjbList[params.index].id
                }, function () {
                  _this.snjbList.splice(params.index, 1);

                  _this.$Message.success("删除成功！");

                  _this.total = _this.total - 1;
                });
              }
            }
          }, [h("Button", {
            props: {
              type: "error",
              size: "small"
            }
          }, "删除")])]);
        }
      }],
      snjbList: [],
      pageIndex: 1,
      pageSize: 10,
      total: 0
    };
  },
  methods: {
    addModalClose: function addModalClose() {
      this.addModal.show = false;
      this.getList();
    },
    editModalClose: function editModalClose() {
      this.editModal.show = false;
      this.getList();
    },
    detailModalClose: function detailModalClose() {
      this.detailModal.show = false;
      this.getList();
    },
    getDetail: function getDetail(obj, okfn) {
      var _this2 = this;

      //    let tempobj = {
      //      projectId: this.ROLE.projectId,
      //      pileNum: obj.pile
      //    };
      snjbApi.getSnjbById({
        id: obj.id,
        okfn: okfn
      }, function (data) {
        console.log("--------------------------------------------------------------");

        if (!data.result.tryDataUrl) {
          data.result.tryDataUrl = [];
        } else {
          data.result.tryDataUrl = JSON.parse(data.result.tryDataUrl);
        }

        _this2.detail = data.result;
        okfn && okfn(); //                    this.formItem.barCageCountImg = imgs2 && imgs2.length ? imgs2 : []
      });
    },
    // 获取列表的方法名统一改为getList，为了在选择工程的时候 刷新页面
    getList: function getList() {
      var _this3 = this;

      console.log(this.ROLE);
      snjbApi.getSnjbListByProjectId({
        //        pageIndex: this.pageSize * (this.pageIndex - 1) + 1,
        //        pageSize: this.pageSize,
        data: this.ROLE.projectId - 0
      }, function (data) {
        console.log(data);
        _this3.total = data.result.length;

        if (!data.result.tryDataUrl) {
          data.result.tryDataUrl = [];
        } else {
          data.result.tryDataUrl = JSON.parse(data.result.tryDataUrl);
        }

        _this3.snjbList = data.result;
      });
    },
    pageChange: function pageChange(pageIndex) {
      console.log(pageIndex);
      this.pageIndex = pageIndex;
      this.getList();
    },
    pageSizeChange: function pageSizeChange(pageSize) {
      console.log(pageSize);
      this.pageSize = parseInt(pageSize);
      this.getList();
    }
  },
  mounted: function mounted() {}
};